import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import {
  cancelOrder,
  downloadInvoice,
  getAllOrders,
  getOrderById,
  getStatusHistory,
} from "../api/orderApis";

const useOrders = () => {
  return useQuery({
    queryKey: ["orders"],
    queryFn: getAllOrders,
    staleTime: 1000 * 60 * 5,
  });
};

const useOrderById = (orderId) => {
  return useQuery({
    queryKey: ["order", orderId],
    queryFn: () => getOrderById(orderId),
    staleTime: 1000 * 60 * 5,
    enabled: !!orderId,
  });
};

const useOrderStatus = (orderId) => {
  return useQuery({
    queryKey: ["orderStatus", orderId],
    queryFn: () => getStatusHistory(orderId),
    staleTime: 1000 * 60 * 5,
    enabled: !!orderId,
  });
};

const useCancelOrder = (orderId) => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: cancelOrder,
    onSuccess: () => {
      queryClient.invalidateQueries(["orders"]);
      queryClient.invalidateQueries(["order", orderId]);
      queryClient.invalidateQueries(["orderStatus", orderId]);
    },
  });
};

const useDownloadInvoice = (orderId) => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: downloadInvoice,
    onSuccess: () => {
      queryClient.invalidateQueries(["order", orderId]);
    },
  });
};

export {
  useOrders,
  useOrderById,
  useOrderStatus,
  useCancelOrder,
  useDownloadInvoice,
};
